










































































































.v-select-language--section > img {
  left: 0;
  position: absolute;
}

.v-product-info--section li {
  border-radius: 20px;
  overflow: hidden;
}

h1 {
  color: #4e5c67;
}

ul {
  list-style-type: none;
  padding: 0;
}

.v-flags {
  padding-bottom: 40px;
  padding-top: 40px;

  .btn {
    padding-bottom: 1px;
  }
}

.v-flags h1 {
  margin-bottom: 30px;
}

.v-flags .btn img {
  width: 70px;
}
.v-flags .v-flex {
  align-items: center;
  display: flex;
  flex-flow: wrap;
}

.v-flags .btn span {
  color: #a70632;
  text-align: left;
  padding-bottom: 3px;
  padding-left: 20px;
  width: 100%;
}

.v-flags a {
  display: flex;
  flex-flow: row;
  width: 100%;
}

.v-flags li {
  flex: 1 0 50%;
  padding: 0 10px;

  &:nth-child(n + 3) {
    margin-top: 20px;
  }
}

.v-flags .btn-light {
  box-shadow: 12px 11px 20px 0px #e4e4e4;
  flex-flow: row;
}

@media screen and (min-width: 640px) {
  .v-flags .btn span {
    font-size: 32px;
    line-height: 32px;
  }
}

@media screen and (min-width: 1500px) {
  .v-flags .btn span {
    font-size: 36px;
    line-height: 36px;
  }
}

@media screen and (max-width: 959px) {
  .v-flags .btn span {
    font-size: 28px;
    line-height: 28px;
  }

  .v-flags .v-flex {
    flex-wrap: wrap;
  }

  .v-flags .btn span {
    font-size: 24px;
    line-height: 24px;
    padding-top: 10px;
    padding-left: 0;
    text-align: center;
  }
}
